@import url(https://fonts.googleapis.com/css2?family=Quicksand&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Quicksand&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: 'Quicksand', sans-serif;
}

body {
  margin: 0;
  background-color: #0b0c10;
}
.input {
  margin: 2rem auto;
  padding: 2rem 1rem 1rem 1rem; 
  width: 90%;
  max-width: 40rem;
}

.input-field {
    position: relative;
    margin-bottom: 1rem;
}

.form-label {
  position: absolute;
  left: 1rem;
  top: 1.2rem;
  padding: 0 0.5rem;
  color: #c5c6c7;
  cursor: text;
  background-color: #1f2833;
  transition: top 200ms ease-in,
    left 200ms ease-in,
    font-size 200ms ease-in;
}

.form-input {
    width: 100%;
    height: 100%;
    border: 2px solid #c5c6c7;
    border-radius: 10px;
    font-family: 'Quicksand', sans-serif;
    color: #c5c6c7;
    outline: none;
    background: none;
    padding: 1.25rem;
    box-shadow: none;
}

.form-input:hover, .form-input:focus {
  border-color: #66fcf1;
}

.form-input:not(:-ms-input-placeholder).form-input:not(:focus) ~.form-label {
    font-size: 0.8rem;
    top: -0.5rem;
    left: 0.8rem;
    color: #66fcf1;
}

.form-input:focus ~ .form-label,
.form-input:not(:placeholder-shown).form-input:not(:focus) ~.form-label {
    font-size: 0.8rem;
    top: -0.5rem;
    left: 0.8rem;
    color: #66fcf1;
} 

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type=number] {
  -moz-appearance: textfield;
}
.card {
    background-color: #1f2833;
    box-shadow: 0 0 8px rgba(0, 0, 0, 0.25);
    border-radius: 10px;
}

.custom-button {
    font-family: 'Quicksand', sans-serif;
    -webkit-appearance: none;
            appearance: none;
    background-color: transparent;
    border: 2px solid #c5c6c7;
    border-radius: 5px;
    box-sizing: border-box;
    color: #c5c6c7;
    cursor: pointer;
    display: inline-block;
    font-size: 14px;
    line-height: normal;
    margin: 0;
    min-height: 44px;
    min-width: 0;
    font-weight: bold;
    outline: none;
    padding: 12px 20px;
    text-align: center;
    text-decoration: none;
    transition: all 300ms cubic-bezier(.23, 1, 0.32, 1);
    -ms-user-select: none;
        user-select: none;
    -webkit-user-select: none;
    touch-action: manipulation;
    will-change: transform;
  }
  
  .custom-button:disabled {
    pointer-events: none;
  }
  
  .custom-button:hover {
    color: #66fcf1;
    background-color: #0b0c10;
    box-shadow: rgba(0, 0, 0, 0.25) 0 8px 15px;
    border: 2px solid #66fcf1;
    -webkit-transform: translateY(-2px);
            transform: translateY(-2px);
  }
  
  .custom-button:active {
    box-shadow: none;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }

.backdrop {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    z-index: 10;
    background: rgba(0,0,0,0.75);
}

.modal {
    position: fixed;
    top: 25vh;
    left: 35%;
    width: 30%;
    z-index: 100;
    overflow: hidden;
}

.header {
    background: #66fcf1;
    padding: 1rem;
}
  
.header h2 {
    margin: 0;
    font-weight: bold;
    color: #1f2833;
}
  
.content {
    padding: 1rem;
}

.content p {
    color: #c5c6c7;
}
  
.actions {
    padding: 1rem;
    display: flex;
    justify-content: flex-end;
}
.users {
    margin: 2rem auto;
    width: 90%;
    max-width: 40rem;
}

.users ul {
    list-style: none;
    padding: 1rem;
}

.users li {
    color: #c5c6c7;
    border: 2px solid #45a29e;
    border-radius: 5px;
    margin: 0.5rem 0;
    padding: 0.5rem;
}
